/* MusicLabPage.css */

.music-lab-container {
  background-color: #121212;
  color: white;
  min-height: 100vh;
  padding: 2rem;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  text-align: center;
  box-sizing: border-box;
}

.music-lab-title {
  font-size: 2.5rem;
  color: #1db954;
  margin-bottom: 2.5rem;
  font-weight: bold;
  letter-spacing: 1px;
}



.music-lab-playlists-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 2rem;
}

.music-lab-block {
  background-color: #181818;
  padding: 1.5rem;
  border-radius: 16px;
  width: 300px;
  box-shadow: 0 0 10px rgba(0,0,0,0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.playlist-block-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: white;
  background: linear-gradient(90deg, #1db954, #1ed760);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.playlist-cover {
  width: 100%;
  border-radius: 12px;
  margin-bottom: 1rem;
}

.playlist-time-input label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.playlist-time-input input {
  width: 60px;
  padding: 6px;
  border: none;
  border-radius: 8px;
  background-color: #333;
  color: white;
  text-align: center;
}

.playlist-time-input span {
  margin: 0 4px;
}

.playlist-time-input {
  margin-bottom: 1rem;
}

.playlist-shuffle-toggle {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.playlist-shuffle-toggle input[type="checkbox"] {
  transform: scale(1.2);
}

.playlist-selected-name {
  font-size: 1rem;
  font-weight: 600;
  color: #d8d8d8;
  background-color: #1d1d1d;
  padding: 6px 12px;
  border-radius: 12px;
  margin-bottom: 1rem;
}

.music-lab-controls {
  margin-top: 2rem;
}

.music-lab-button {
  font-size: 2rem;
  padding: 16px 32px; /* 세로 16px, 가로 32px */
  border: none;
  border-radius: 24px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.1s ease;
  font-weight: bold;
  background-color: #1db954;
  color: white;
}

.music-lab-button.start {
  background-color: #1db954;
  color: white;
}

.music-lab-button:hover {
  background-color: #1aa34a;
  transform: scale(1.03);
}
.music-lab-button.stop {
  background-color: #ff4c4c;
  color: white;
}

.music-lab-button.stop:hover {
  background-color: #ff6666;
}

.music-lab-status {
  margin-top: 1.5rem;
  font-size: 1rem;
  color: #ccc;
}
.playlist-cover-wrapper {
  width: 100%;
  aspect-ratio: 1 / 1; /* 정사각형 */
  margin-bottom: 1rem;
  position: relative;
}

.playlist-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 12px;
}
/* playlist search button */
.search-playlist-button {
  margin-top: 10px;
  padding: 8px 14px;
  background-color: #1db954;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.1s ease;
}

.search-playlist-button:hover {
  background-color: #1aa34a;
  transform: scale(1.03);
}

.search-playlist-button:active {
  transform: scale(0.98);
}
/* 플레이스홀더 스타일 */
.playlist-cover.placeholder {
  background-color: #2a2a2a;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  color: #999;
  font-size: 0.9rem;
  font-style: italic;
}
/* modal */
/* 모달 배경 어둡게 */
.modal-overlay {
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

/* 모달 내부 */
.modal-content {
  background: #121212;
  color: white;
  padding: 24px;
  border-radius: 16px;
  width: 420px;
  max-height: 80vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.6);
  animation: fadeIn 0.3s ease-in-out;
}

.modal-search-row {
  display: flex;
  gap: 8px;
  margin-bottom: 16px;
  align-items: center;
}

/* 모달 애니메이션 */
@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.95); }
  to { opacity: 1; transform: scale(1); }
}

/* 닫기 버튼 */
.close-btn {
  position: absolute;
  top: 12px;
  right: 16px;
  background: transparent;
  border: none;
  font-size: 20px;
  color: white;
  cursor: pointer;
}

/* 입력창 + 버튼 */
.modal-content input[type="text"] {
  width: calc(100% - 90px);
  padding: 10px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
  background-color: #2a2a2a;
  color: white;
  margin-right: 8px;
}

.modal-content button {
  padding: 10px 14px;
  font-size: 14px;
  background-color: #1db954;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.modal-content button:hover {
  background-color: #1aa34a;
}

/* 검색 결과 리스트 */
.modal-playlist-results {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

/* 개별 플레이리스트 */
.modal-playlist-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  background-color: #1e1e1e;
  transition: background 0.2s ease;
}

.modal-playlist-item:hover {
  background-color: #2a2a2a;
}

.modal-playlist-item img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  border-radius: 8px;
}

.modal-playlist-item span {
  font-size: 16px;
  font-weight: 500;
  color: #fff;
  text-align: left;
}

/* 입력창: 넓게 */
.modal-playlist-search-input {
  flex: 1;
  padding: 10px 12px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
  background-color: #2a2a2a;
  color: white;
  outline: none;
  box-sizing: border-box;
  transition: border 0.2s ease, background-color 0.2s ease;
}

.modal-playlist-search-input:focus {
  border: 1px solid #1db954;
  background-color: #333;
}


/* 버튼: 고정 너비 */
.modal-search-row button {
  padding: 10px 14px;
  width: 90px;
  font-size: 14px;
  background-color: #1db954;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  flex-shrink: 0;
  transition: background 0.2s ease, transform 0.1s ease;
}

.modal-search-row button:hover {
  background-color: #1aa34a;
  transform: scale(1.03);
}



/* interval save, 불러오기 button */
.interval-save-buttons {
  display: flex;
  gap: 12px;
  margin: 20px 0;
  justify-content: center;
}


.interval-btn {
  background-color: #1db954;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 10px 18px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.1s ease;
}

.interval-btn:hover {
  background-color: #1aa34a;
  transform: scale(1.05);
}

.interval-btn:active {
  transform: scale(0.95);
}

/* 저장된 인터벌 삭제 버튼 : delete saved interval button */
.delete-preset-btn {
  background: transparent;
  border: none;
  color: #999;
  font-size: 16px;
  cursor: pointer;
  transition: color 0.2s ease;
}

.delete-preset-btn:hover {
  color: #ff4d4d;
}

.modal-save-btn {
  padding: 10px 18px;
  font-size: 15px;
  background-color: #1db954;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.2s ease, transform 0.1s ease;
}

.modal-save-btn:hover {
  background-color: #1aa34a;
  transform: scale(1.03);
}

.modal-save-btn:active {
  transform: scale(0.96);
}


.modal-save-Interval-input {
  width: 100%;
  padding: 10px 12px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
  background-color: #2a2a2a;
  color: white;
  outline: none;
  margin-bottom: 12px;
  box-sizing: border-box;
  transition: border 0.2s ease, background-color 0.2s ease;
}

.modal-save-Interval-input:focus {
  border: 1px solid #1db954;
  background-color: #333;
}
