.dashboard-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;  /* 중앙 정렬 (세로) */
  align-items: center;      /* 중앙 정렬 (가로) */
  background-color: #121212;
  color: #ffffff;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  text-align: center;
  box-sizing: border-box;
  padding: 2rem;
  position: relative;
}

.dashboard-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}

.dashboard-title {
  font-size: 2.5rem;
  color: #1db954;
}

.dashboard-music-button {
  margin-top: 1rem;
  padding: 12px 24px;
  font-size: 1rem;
  background-color: #1db954;
  color: #fff;
  border: none;
  border-radius: 24px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.dashboard-music-button:hover {
  background-color: #1ed760;
}

.dashboard-logout-button {
  position: absolute;
  bottom: 2rem;
  padding: 10px 20px;
  font-size: 0.95rem;
  background-color: #535353;
  color: #fff;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.dashboard-logout-button:hover {
  background-color: #6c6c6c;
}
