/* IntervalEntry.css */

.interval-entry-container {
  background-color: #121212;
  color: white;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  text-align: center;
  padding: 2rem;
  box-sizing: border-box;
}

.interval-entry-title {
  font-size: 2rem;
  font-weight: bold;
  color: #1db954;
  margin-bottom: 2rem;
}

.interval-entry-next {
  margin-top: 1rem;
}

.interval-entry-button {
  background-color: #17720b; /* purple-600 */
  color: white;
  padding: 12px 24px;
  font-size: 1rem;
  border: none;
  border-radius: 24px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.interval-entry-button:hover {
  background-color: #7cff98;
}
